@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* { box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  background-color: #2c3e50;
  color: #ecf0f1;
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  overflow-y: scroll;
}

header {
  position: sticky;
  top: 0;
}

nav {
  background-color: #ecf0f1;
  height: 50px;
  text-align: center;
}

nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

nav ul a {
  padding: 2px 8px;
  color: #2c3e50;
  font-size: 18px;
  text-decoration: none;
  line-height: 50px;
}

nav ul a:hover:not(.active) {
  text-decoration: underline;
}

nav ul li {
  display: inline-block;
  margin: 0 12px;
}

nav .active {
  border: 2px solid #2c3e50;
  border-radius: 4px;
  margin: -2px;
}

.home-banner {
  position: relative;
  background-image: url('https://via.placeholder.com/1920x1080/454545/454545');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 540px;
  z-index: -1;
}

.home-banner h2 {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 4em;
}

.flex-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
}

.flexbox {
  width: 25%;
}

.content {
  padding: 24px;
  margin: 24px;
  background-color: #ecf0f1;
  color: #2c3e50;
  cursor: pointer;
}

.content h2 {
  min-height: 50px;
}

.content:hover {
  box-shadow: 1px 1px 10px #3498db;
}

h2, p {
  text-align: center;
}

h2 {
  margin-top: 32px;
  font-size: 18px;
}

.data-title {
  font-variant: small-caps;
  font-size: 18px;
}

.data-title ul {
  list-style: none;
}

.data-title li {
  padding: 6px 12px;
}

.data-title p {
  padding-right: 38px;
  margin-top: 2px;
  text-align: left;
}

.show-more {
  padding-bottom: 38px;
  text-align: center;
}

.show-more button {
  padding: 6px 12px;
  border: none;
  background-color: #ecf0f1;
  color: #2c3e50;
  text-transform: uppercase;
  font-size: 18px;
  cursor: pointer;
  outline: none;
}

.show-more button:hover {
  background-color: #2c3e50;
  border: 2px solid #ecf0f1;
  margin: -2px;
  color: #ecf0f1;
}

footer {
  width: 100%;
  background-color: #ecf0f1;
  color: #2c3e50;
}

footer p {
  padding: 18px 0;
  margin: 0;
}

@media only screen and (max-width: 768px) {
  .flexbox { width: 50%;}
}

@media only screen and (max-width: 425px) {
  .flexbox { width: 100%;}
}